<template>
  <div class="container">
    <Header
      title="Task Tracker"
      @toggle-add-task="toggleAddTask"
      :showAddTask="showAddTask"
    />
    <AddTask
      :task="task"
      :errors="errors"
      ref="form"
      @add-task="addTask"
      @update-task="updateTask"
      @set-focus="setFocus"
      v-show="showAddTask"
    />
    <Tasks
      :tasks="tasks"
      @delete-task="deleteTask"
      @edit-task="editTask"
      @toggle-reminder="toggleReminder"
    />
  </div>
</template>

<script>
import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";

export default {
  name: "App",
  components: {
    Header,
    Tasks,
    AddTask,
  },
  data() {
    return {
      task: {
        id: "",
        text: "",
        day: "",
        reminder: false,
      },
      tasks: [],
      showAddTask: false,
      errors: {},
    };
  },
  methods: {
    setFocus(errors) {
      const firstError = Object.keys(errors)[0];
      this.$refs.form.$refs[firstError].focus();
    },
    resetForm() {
      this.task.id = "";
      this.task.text = "";
      this.task.day = "";
      this.task.reminder = false;
      this.errors = {};
    },
    confirmDelete(task) {
      return confirm(
        `You have chosen to delete ${task.text} on ${task.day}. Click 'OK' to confirm or 'CANCEL' to abort.`
      );
    },
    toggleAddTask() {
      this.showAddTask = !this.showAddTask;

      if (this.showAddTask) {
        this.resetForm();
      }
    },
    processFetch(url, obj = null, cb = null) {
      const params = obj
        ? {
            method: "POST",
            headers: { "Content-type": "application/json" },
            body: JSON.stringify(obj),
          }
        : null;
      fetch(url, params)
        .then((response) => {
          if (response.ok) {
            return response;
          } else {
            throw new Error(`http error: ${response.status}`);
          }
        })
        .then((response) => response.json())
        .then((response) => {
          if (response.succeeded) {
            if (response.data) {
              this.tasks = response.data;
            } else {
              this.fetchTasks();

              if (cb) {
                cb();
              }
            }
          } else {
            if (typeof response.error === "object") {
              this.errors = response.error;
              this.setFocus(this.errors);
            } else {
              throw new Error(`database error: ${response.error}`);
            }
          }
        })
        .catch((error) => {
          console.log(error.message);
        });
    },
    addTask(taskToAdd) {
      this.processFetch("tasks/add", taskToAdd, this.resetForm);
    },
    editTask(taskToEdit) {
      if (!this.showAddTask) {
        this.showAddTask = true;
      }

      this.task = { ...taskToEdit };
    },
    deleteTask(taskToDelete) {
      if (this.confirmDelete(taskToDelete)) {
        this.processFetch("tasks/delete", taskToDelete);
      }
    },
    updateTask(taskToUpdate) {
      this.processFetch("tasks/update", taskToUpdate, this.resetForm);
    },
    toggleReminder(taskToUpdate) {
      this.processFetch("tasks/toggle", taskToUpdate);
    },
    fetchTasks() {
      this.processFetch("tasks");
    },
  },
  created() {
    this.fetchTasks();
  },
};
</script>
