<template>
  <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <label>Task</label>
      <input
        type="text"
        v-model="task.text"
        name="text"
        ref="text"
        placeholder="Add Task"
        :class="errors.text ? 'invalid' : ''"
      />
      <div v-if="errors.text" class="error-icon">
        <i class="fas fa-exclamation" />
      </div>
      <div v-if="errors.text" class="error-message">
        {{ errors.text }}
      </div>
    </div>
    <div class="form-control">
      <label>Day &amp; Time</label>
      <input
        type="text"
        v-model="task.day"
        name="day"
        ref="day"
        placeholder="Add Day &amp; Time"
        :class="errors.day ? 'invalid' : ''"
      />
      <div v-if="errors.day" class="error-icon">
        <i class="fas fa-exclamation" />
      </div>
      <div v-if="errors.day" class="error-message">
        {{ errors.day }}
      </div>
    </div>
    <div class="form-control form-control-check">
      <input
        type="checkbox"
        v-model="task.reminder"
        name="reminder"
        id="reminder"
      />
      <label for="reminder">Set Reminder</label>
    </div>
    <input
      type="submit"
      :value="task.id ? 'Update Task' : 'Save Task'"
      class="btn btn-block"
    />
  </form>
</template>

<script>
export default {
  name: "AddTask",
  props: {
    task: Object,
    errors: Object,
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      this.$emit(this.task.id ? "update-task" : "add-task", this.task);
    },
  },
};
</script>
