<template>
  <div :key="task.id" v-for="task in tasks">
    <Task
      :task="task"
      @delete-task="$emit('delete-task', task)"
      @edit-task="$emit('edit-task', task)"
      @toggle-reminder="$emit('toggle-reminder', task)"
    />
  </div>
</template>

<script>
import Task from "./Task";

export default {
  name: "Tasks",
  props: {
    tasks: Array,
  },
  components: {
    Task,
  },
  emits: ["edit-task", "delete-task", "toggle-reminder"],
};
</script>
