<template>
  <div
    :class="[task.reminder ? 'reminder' : '', 'task']"
    @dblclick="$emit('toggle-reminder', task)"
  >
    <h3>
      {{ task.text }}
      <div>
        <i
          class="fas fa-pencil-alt"
          title="edit task"
          @click="$emit('edit-task', task)"
        />
        <i
          class="fas fa-times"
          title="delete task"
          @click="$emit('delete-task', task)"
        />
      </div>
    </h3>
    <p>{{ task.day }}</p>
  </div>
</template>

<script>
export default {
  name: "Task",
  props: {
    task: Object,
  },
};
</script>
